* {
  padding: 0;
  margin: 0;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
      user-select: none;
}

html, body, #root {
  height: 100%;
  overflow-y: hidden;
  background: #F88;
}
  
.demo-container {
    position: absolute;
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    display: -webkit-flex;
    -webkit-justify-content: center;
    -webkit-align-items: center;
    overflow-y: auto;
  }
  
  .list {
    width: 320px;
    height: 400px;
  }

  .list-item {
    position: absolute;
    background-color: #f0f0f0;
    width: 320px;
    overflow: visible;
    pointer-events: auto;
    -webkit-transform-origin: 50% 50% 0px;
        -ms-transform-origin: 50% 50% 0px;
            transform-origin: 50% 50% 0px;
    border-radius: 4px;
    color: rgb(153, 153, 153);
    height: 70px;
    line-height: 70px;
    padding-left: 32px;
    font-size: 24px;
    font-weight: 400;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
  }



